import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {  AdminTallerListResolver, AdminTallerSolicitudesListResolver, } from '../admin.resolver';
import { TallerAdminComponent } from './taller-admin.component';
import { TallerAdminSolicitudesComponent } from './taller-admin-solicitudes/taller-admin-solicitudes.component';
import { TallerAdminListComponent } from './taller-admin-list/taller-admin-list.component';

const routes: Routes = [
  {
    path: '',
    component: TallerAdminComponent,
    children:[
      {
        path: 'list',
        component: TallerAdminListComponent,
        resolve:{
          talleres: AdminTallerListResolver,
        },
        data: {
          title: "Talleres",
          breadcrumb: "Lista"
        },
      },
      {
        path: 'solicitudes',
        component: TallerAdminSolicitudesComponent,
        resolve:{
          talleres: AdminTallerSolicitudesListResolver,
        },
        data: {
          title: "Talleres",
          breadcrumb: "Solicitudes"
        },
      },
    ],
    data: {
      title: "Talleres",
      breadcrumb: "Talleres"
    },
    // resolve: {
    //   manillas: AdminManillasResolver
    // }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TallerAdminRoutingModule { }
