import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from "ng-gallery";
import { Lightbox } from "ng-gallery/lightbox";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";

@Component({
  selector: "app-ver-taller-admin",
  templateUrl: "./ver-taller.component.html",
  styleUrls: ["./ver-taller.component.scss"],
})
export class VerTallerAdminComponent implements OnInit, OnDestroy {

  @Input() tipo: any;
  @ViewChild("modalTallerAdmin", { static: false })
  ModalTallerAdmin: TemplateRef<any>;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  taller: any = {};

  Toast: any;

  loading: boolean = true;

  acepting: boolean = true;
  limit: any = environment.pagination_limit;
  items: GalleryItem[];


  imagenTemp = 'https://img.freepik.com/vector-premium/imagen-dibujos-animados-hongo-palabra-hongo_587001-200.jpg?w=2000';

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    private modalService: NgbModal,
    private _changeDetectorRef: ChangeDetectorRef,
    private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  }

  ngOnInit(): void {
    this._adminService.tallerSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((taller: any) => {
        this.loading = true;
        if (this.tipo === "solicitudes") {
          this.acepting = true;
        }
        // Update the manilla list
        if (taller !== null) {
          this.getTallerById(taller._id);
        }
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    if (this.modalOpen) {
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  openModal() {
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) {
      // For SSR
      this.modalService.open(this.ModalTallerAdmin, {
          size: "lg",
          ariaLabelledBy: "modal-basic-title",
          centered: true,
          windowClass: "ModalTallerAdmin",
          backdrop: "static",
          keyboard: false,
          scrollable: true
        })
        .result.then(
          (result) => {
            `Result ${result}`;
          },
          (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          }
        );
    }
  }

  private getDismissReason(reason: any) {}

  getTallerById(id: any) {
    this._adminService
      .getTallereById(id)
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this.items = [new ImageItem({ src: response.fotoBase64, thumb: response.fotoBase64 })];
          // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
          const lightboxRef = this.gallery.ref('lightbox');
          // Add custom gallery config to the lightbox (optional)
          lightboxRef.setConfig({
            imageSize: ImageSize.Cover,
            thumbPosition: ThumbnailsPosition.Top
          });

          // Load items into the lightbox gallery ref
          lightboxRef.load(this.items);
          this.taller = response;
          this.loading = false;
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
        }
      );
  }

  aceptarTallerById() {
    if (this.acepting) {
      this.acepting = false;
      this._adminService.aceptarTallerById(this.taller._id).pipe(takeUntil(this._unsubscribeAll))
        .subscribe(
          (response: any) => {
            this.Toast.fire({
              icon: "success",
              title: "El taller ha sido aceptado y puede iniciar sesíon",
            });
            this.updateTalleresList();
          },
          (error) => {
            this.Toast.fire({
              icon: "error",
              title: error.error.message,
            });
            this.modalService.dismissAll();
          }
        );
    }
  }

  updateTalleresList() {
    this._adminService.getTalleresListUpdate(0, this.limit, null).pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this._adminService.updateTalleresSolicitudesList(response);
          this.modalService.dismissAll();
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
        }
      );
  }

  submit(): void {}
}
