import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";

@Component({
  selector: "app-crear-taller-admin",
  templateUrl: "./crear-taller.component.html",
  styleUrls: ["./crear-taller.component.scss"],
})
export class CrearTallerAdminComponent implements OnInit, OnDestroy {
  
  @Input() tipo: any;
  @ViewChild("modalCreateTallerAdmin", { static: false })
  ModalCreateTallerAdmin: TemplateRef<any>;
  @ViewChild("tallerNgForm") tallerNgForm: NgForm;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  taller: any = {};

  Toast: any;

  loading: boolean = true;

  acepting: boolean = true;

  limit: any = environment.pagination_limit;

  tallerForm: FormGroup;

  uploadedFiles:any;

  imageBase64: any = '';

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    private router: Router,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private _adminService: AdminService
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  }

  static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        // if control is empty return no error
        return null;
      }

      // test the value of the control against the regexp supplied
      const valid = regex.test(control.value);

      // if true, return no error (no error), else return error passed in the second parameter
      return valid ? null : error;
    };
  }

  ngOnInit(): void {
    this.tallerForm = this._formBuilder.group({
      name: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      password: [
        "",
        Validators.compose([
          Validators.required,
          CrearTallerAdminComponent.patternValidator(/\d/, { hasNumber: true }),
          CrearTallerAdminComponent.patternValidator(/[A-Z]/, {
            hasCapitalCase: true,
          }),
          CrearTallerAdminComponent.patternValidator(/[a-z]/, {
            hasSmallCase: true,
          }),
          CrearTallerAdminComponent.patternValidator(
            /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
            { hasSpecialCharacters: true }
          ),
          Validators.minLength(8),
        ]),
      ],
      direccion: ["", [Validators.required]],
      telefono: ["", [Validators.required, Validators.pattern("[0-9]*")]],
      fotoBase: [""],
    });
  }

  ngOnDestroy() {
    if (this.modalOpen) {
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  openModal() {
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) {
      // For SSR
      this.modalService
        .open(this.ModalCreateTallerAdmin, {
          size: "lg",
          ariaLabelledBy: "modal-basic-title",
          centered: true,
          windowClass: "ModalCreateTallerAdmin",
          backdrop: "static",
          keyboard: false,
          scrollable: true
        })
        .result.then(
          (result) => {
            `Result ${result}`;
          },
          (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          }
        );
    }
  }

  private getDismissReason(reason: any) {}

  onFileSelected(event){
    this.uploadedFiles = event.target.files[0];
    if(this.uploadedFiles.type === 'image/jpeg' || this.uploadedFiles.type === 'image/png' || this.uploadedFiles.type === 'image/jpg'){
      const reader = new FileReader();
      reader.readAsDataURL(this.uploadedFiles);
      reader.onload = () => {
        let dataN = reader.result.toString();
        // let dataD = '';
        // dataD = dataN.replace(/^data:image\/\w+;base64,/,'');
        this.imageBase64 = dataN;
      };
    }else{
      this.Toast.fire({
        icon: "error",
        title: 'Debe subir un tipo de imagen valido (JPEG, JPG, PNG).',
      });
      this.tallerForm.get('fotoBase').setValue('');
      this.uploadedFiles = '';
    }
  }

  btnCreateTaller() {
    if (!this.tallerForm.disabled) {
      if (!this.tallerForm.valid) {
        this.Toast.fire({
          icon: "error",
          title: 'Debes rellenar todos los campos para crear un taller.',
        });
        // this.disableButton = true;
      } else {
        this.crearTaller();
      }
    }
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  crearTaller() {
    this.acepting = false;
    // Do nothing if the form is invalid
    if (this.tallerForm.invalid) {
      return;
    }

    // Disable the form
    this.tallerForm.disable();

    let data = this.tallerForm.getRawValue();
    data.fotoBase64 = this.imageBase64;
    delete data.fotoBase;

    this._adminService.createTaller(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response: any) => {
          this.Toast.fire({
            icon: "success",
            title: "El taller ha sido creado y ya puede iniciar sesíon",
          });
          this.updateTalleresList();
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
          // enable the form
          this.tallerForm.enable();
        }
      );
  }

  updateTalleresList() {
    this._adminService
      .getTalleresListUpdate(0, this.limit, null)
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this._adminService.updateTalleresList(response);
          this.modalService.dismissAll();
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
        }
      );
  }

  submit(): void {}
}
