import { ViewportScroller } from "@angular/common";
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormControl } from "@angular/forms";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { UserService } from "src/app/core/user/user.service";
import { AdminService } from "../../admin.service";
import Swal from "sweetalert2";
import { VerTallerAdminComponent } from "../modals/ver-taller/ver-taller.component";
import { environment } from "src/environments/environment";

@Component({
  selector: "app-taller-admin-list",
  templateUrl: "./taller-admin-list.component.html",
  styleUrls: ["../taller-admin.component.scss"],
})
export class TallerAdminListComponent implements OnInit, OnDestroy {

  @ViewChild("verTallerModal") VerTallerModal: VerTallerAdminComponent;
  @ViewChild("crearTallerModal") CrearTallerModal: VerTallerAdminComponent;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  talleresCount: number = 0;
  talleresList: any = [];
  page: any = 1;
  limit: any = environment.pagination_limit;
  Toast:any;

  searchInputControl: FormControl = new FormControl('');

  constructor(
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller,
    private _adminService: AdminService
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this._adminService.talleresList$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((talleres: any) => {
        // Update the selected agente
        this.talleresList = talleres.talleres;
        this.talleresCount = talleres.totalDocuments;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
  * On destroy
  */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  onPageChange(event:any){
    if(this.searchInputControl.value !== ''){
      this.getTalleresPaginated(((event - 1)), this.searchInputControl.value);
    }else{
      this.getTalleresPaginated(((event - 1)), null);
    }
  }

  getTalleresPaginated(page:any, name:any){
    this._adminService.getTalleresListUpdate(page, this.limit, name).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._adminService.updateTalleresList(response);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  abrirModalVerTaller(taller: any){
    this._adminService.updateTallerSelected(taller);
    this.VerTallerModal.openModal();
  }

  abrirModalCrearTaller(){
    this.CrearTallerModal.openModal();
  }

  btnBuscarTallerPorNombre(){
    if(this.searchInputControl.value !== ''){
      this.getTalleresPaginated(0, this.searchInputControl.value);
    }
  }

  btnRefresh(){
    this.searchInputControl.setValue('');
    this.getTalleresPaginated(0, null);
  }
}
